$breakpoints: (
  'xs-phone': 320px,
  'phone': 480px,
  'tablet': 768px,
  'desktop': 1024px,
  'widescreen': 1200px
);

// keywords
$media-expressions: (
  'screen': 'screen',
  'print': 'print',
  'handheld': 'handheld',
  'landscape': '(orientation: landscape)',
  'portrait': '(orientation: portrait)',
  'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
  'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)'
);

@import 'vendors/include-media';
@import 'vendors/normalize';

@import 'base/functions';
@import 'base/variables';
@import 'base/mixins';
@import 'base/helpers';
@import 'base/reset';
@import 'base/typography';
@import 'base/forms';

@import 'layout/header';
@import 'layout/common';
@import 'layout/footer';

html,
body {
    height: 100%;
}
body {
    background: #000;
    min-height: 760px;
}
.wrapper {
    min-height: 100%;
    position: relative;
    overflow: hidden;
}
.snow {
    top:-50px;
    left:0px;
    position:absolute;
    z-index:200;
    color:#fff;   
}
.road {
    width: 500%;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 615px;
    background: url("../images/road-add.svg") repeat-x;
    animation: road 6s linear infinite;
}
.cloud {
    position: absolute;
    left: 20px;
    top: 20px;
    &.cloud02 {
        right: 10px;
        top: 145px;
        left: inherit;
    }
}
.sun {
    width: 106px;
    height: 106px;
    background: #fff8a7;
    border-radius: 50%;
    position: absolute;
    top: 50px;
    right: 210px;
}
.mound {
    position: absolute;
    bottom: 250px;
    right: 400px;
    z-index: -1;
}
.shrub {
    position: absolute;
    bottom: 0;
    right: 85%;
    z-index: 10;
    &.shrub02 {
        bottom: 225px;
        left: inherit;
        right: 5%;  
    }
}

.tree {
    position: absolute;
    right: 5%;
    bottom: 230px;
    &.tree02 {
        right: 84%;
        bottom: 240px;
    }
}
.snow {
    position: absolute;
    bottom: 90px;
    right: 20%;
    &.snow02 {
        right: 50%;
        bottom: 130px;
    }
}
.car-block {
    position: absolute;
    bottom: 125px;
    left: 345px;
    .wheel {
        width: 73px;
        height: 73px;
        position: absolute;
        bottom: -25px;
        left: 25px;
        animation: rotateInfinite 1s linear infinite;
        &.wheel02 {
            right: 25px;
            bottom: -25px;
            left: initial;
        }
    }
    .smoke {
        position: absolute;
        left: -65px;
        bottom: 15px;
    }
}
.santa {
    position: absolute;
    bottom: 163px;
    left: 448px;
}
@keyframes rotateInfinite{
  0%
  {
    transform: rotate(0deg);
  }
  50%
  {
    transform: rotate(180deg);
  }
  100%
  {
    transform: rotate(360deg);
  }
}
@keyframes road
{
    100%{
        transform: translateX(-2000px);
    }
}
@keyframes moveThegroundRight
{
 90%
  {
    opacity:1;
   left:100%;
  }
  95%,100%
  {
    left:1300%;
    opacity:0;
  }
}
