body {
  color: #333;
  background: #fff;
  font-size: 16px;
  line-height: 1.2;
  font-family: "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.gm-style img {
  max-width: none;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
.h {
  font-family: inherit;
  font-weight: bold;
  margin: 0 0 0.5em;
  color: inherit;
}

h1,
.h1 {
  font-size: 30px;
}

h2,
.h2 {
  font-size: 27px;
}

h3,
.h3 {
  font-size: 24px;
}

h4,
.h4 {
  font-size: 21px;
}

h5,
.h5 {
  font-size: 17px;
}

h6,
.h6 {
  font-size: 15px;
}

p {
  margin: 0 0 1em;
}

a {
  color: #39f;
}

a:hover, a:focus {
  text-decoration: none;
}

form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  box-sizing: border-box;
  border: 1px solid #999;
  padding: 0.4em 0.7em;
}

input[type='text']:focus,
input[type='tel']:focus,
input[type='email']:focus,
input[type='search']:focus,
input[type='password']:focus,
textarea:focus {
  border-color: #333;
}

input[type='text']::placeholder,
input[type='tel']::placeholder,
input[type='email']::placeholder,
input[type='search']::placeholder,
input[type='password']::placeholder,
textarea::placeholder {
  color: #999;
}

select {
  -webkit-border-radius: 0;
}

textarea {
  resize: vertical;
  vertical-align: top;
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer;
}

html,
body {
  height: 100%;
}

body {
  background: #000;
  min-height: 760px;
}

.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden;
}

.snow {
  top: -50px;
  left: 0px;
  position: absolute;
  z-index: 200;
  color: #fff;
}

.road {
  width: 500%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 615px;
  background: url("../images/road-add.svg") repeat-x;
  animation: road 6s linear infinite;
}

.cloud {
  position: absolute;
  left: 20px;
  top: 20px;
}

.cloud.cloud02 {
  right: 10px;
  top: 145px;
  left: inherit;
}

.sun {
  width: 106px;
  height: 106px;
  background: #fff8a7;
  border-radius: 50%;
  position: absolute;
  top: 50px;
  right: 210px;
}

.mound {
  position: absolute;
  bottom: 250px;
  right: 400px;
  z-index: -1;
}

.shrub {
  position: absolute;
  bottom: 0;
  right: 85%;
  z-index: 10;
}

.shrub.shrub02 {
  bottom: 225px;
  left: inherit;
  right: 5%;
}

.tree {
  position: absolute;
  right: 5%;
  bottom: 230px;
}

.tree.tree02 {
  right: 84%;
  bottom: 240px;
}

.snow {
  position: absolute;
  bottom: 90px;
  right: 20%;
}

.snow.snow02 {
  right: 50%;
  bottom: 130px;
}

.car-block {
  position: absolute;
  bottom: 125px;
  left: 345px;
}

.car-block .wheel {
  width: 73px;
  height: 73px;
  position: absolute;
  bottom: -25px;
  left: 25px;
  animation: rotateInfinite 1s linear infinite;
}

.car-block .wheel.wheel02 {
  right: 25px;
  bottom: -25px;
  left: initial;
}

.car-block .smoke {
  position: absolute;
  left: -65px;
  bottom: 15px;
}

.santa {
  position: absolute;
  bottom: 163px;
  left: 448px;
}

@keyframes rotateInfinite {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes road {
  100% {
    transform: translateX(-2000px);
  }
}

@keyframes moveThegroundRight {
  90% {
    opacity: 1;
    left: 100%;
  }
  95%, 100% {
    left: 1300%;
    opacity: 0;
  }
}
